.subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
  &--lighter {
  color: #222222;
  }
  &--light {
    color: #ffffff;
  }
  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 26px;
  }
  @media (min-width: $desktop-width) {
    line-height: 21px;
  }
}
